@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-background text-text text-center;
  }
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-lg font-semibold transition duration-300;
  }

  .btn-primary {
    @apply bg-primary text-text hover:bg-opacity-80;
  }

  .btn-secondary {
    @apply bg-secondary text-text hover:bg-opacity-80;
  }

  .btn-accent {
    @apply bg-accent text-background hover:bg-opacity-80;
  }

  .btn-tall {
    @apply h-12;
  }

  .container {
    @apply mx-auto px-4 py-6 bg-secondary text-text text-center rounded-lg;
  }

  .container-sm {
    @apply max-w-sm;
  }

  .container-md {
    @apply max-w-md;
  }

  .container-lg {
    @apply max-w-lg;
  }

  .container-xl {
    @apply max-w-xl;
  }

  .text-animation {
    @apply animate-text bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent;
  }

  .input {
    @apply px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent;
  }
}
